.rain {
  width: 100%;
  height: 100%;
  z-index: 2;
  position: relative;
  left: 0;
}

.rain.back-row {
  z-index: 1;
  opacity: .5;
  bottom: 60px;
}

body.back-row-toggle .rain.back-row {
  display: block;
}

.drop {
  width: 15px;
  height: 120px;
  pointer-events: none;
  animation: drop .5s linear infinite;
  position: absolute;
  bottom: 100%;
}

@keyframes drop {
  0% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(90vh);
  }

  100% {
    transform: translateY(90vh);
  }
}

.stem {
  width: 2px;
  height: 60%;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, .25));
  margin-left: 7px;
  animation: stem .5s linear infinite;
}

@keyframes stem {
  0% {
    opacity: 1;
  }

  65% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.splat {
  width: 15px;
  height: 10px;
  opacity: 1;
  border-top: 2px dotted rgba(255, 255, 255, .5);
  border-radius: 50%;
  animation: splat .5s linear infinite;
  display: none;
  transform: scale(0);
}

body.splat-toggle .splat {
  display: block;
}

@keyframes splat {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  80% {
    opacity: 1;
    transform: scale(0);
  }

  90% {
    opacity: .5;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}

.splat-toggle {
  top: 20px;
}

.back-row-toggle {
  padding-top: 14px;
  line-height: 12px;
  top: 90px;
}

